    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    color: #6F5D6C;
    font-weight: 600;
}

.anno-lang-option {
    border: 1px solid gray;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    /* align-items: center; */
}

.toggle-anno-view {
    display: flex;
    position: relative;
    align-items: center;
    padding: 18px 8px; 
    
    width: 180px;
}

.eyecon {
    height: 30px;
    width: 30px;
    cursor: pointer;
    /* border: 1px solid red; */
}
.toggle-anno-view span {
    margin-left: 8px;
    position: absolute;
    right: 18px;
}
.anno-lang-option select {
    border-radius: 0;
    border: none;
    border-left: 1px solid gray;
    padding: 12px;
    font-size: 1.9rem;
}

.toggle-popular-anno {
    display: flex;
    border: 1px solid gray;
    align-items: center;
}

.toggle-popular-anno .check-box-area {
    padding: 8px;
}

.toggle-popular-anno .toggle-popular {
    height: 100%;
    position: relative;
    border-left: 1px solid gray;
    display: flex;
    /* padding: 8px; */
    width: 60px;
    align-items: center;
    justify-content: center;
}

.toggle-popular-anno .toggle-popular input {
    width: 100%;
    text-align: center;
    /* padding-left: 12px; */
    font-size: 1.2rem;
    border: none;
}

.edit-annotation {
    border: 1px solid lightgray;
    padding: 8px 12px 8px 8px;
    display: flex;
    align-items: center;
    color: lightgray;
}

.edit-annotation .icon {
    margin-right: 8px;
}

.document {
    position: relative;
    margin-top: 24px;
    width: 90%;
    text-align: center;
}

.document img.annotated {
    border: 1px solid gray;
    width: 100%;
}


.annotation-more-info {
    display: flex;
    width: 90%;
    margin-top: 24px;
}

.participant-history {
    border: 1px solid gray;
    width: 55%;
    margin-right: 3%;
    padding: 8px 16px;
    height: 180px;
    overflow: auto;
}

.participant-history h4 {
    color: #400B36;
}

.participant-history .annotator-info {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    justify-content: space-between;
    border-bottom: 0.5px solid lightgray;
}

.annotator-action {
    display: flex;
    align-items: center;
}

.annotator-action img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 0.6px solid gray;
    margin-right: 8px;
}

.other-annotations {
    border: 1px solid gray;
    width: 40%;
    height: 180px;
    overflow: auto;
    padding: 8px 16px;
}

.other-annotations .annotated-page {
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    box-shadow: 1px 3px 6px gray;
    width: 90%;
    height: 140px;
    margin: 2% auto;
}

.other-annotations .annotated-page img {
    height: 100%;
    width: 40%;
    object-fit: cover;
    object-position: top;
    overflow: hidden;
}

.other-annotations .annotated-page .page-info {
    padding-left: 5%;
}

.other-annotations .annotated-page .page-info h4 {
    color: #400B36;
}

